        { text: 'Básico', color: 'red', nivel: '1' },
      ],
    };
  },
  computed: {
    sortConocimientoByNivel() {
      const mapeo = this.conocimientos.map((item) => {
        const sortDato = this.sortByProperty(item.dato, 'nivel');
        const dato = sortDato.map(data =>
          Object.assign(data, this.getValue(data.nivel)),
        );
        return { titulo: item.titulo, dato };
      });
      return mapeo;
    },
  },
  methods: {
    getValue(nivel) {
      const valor = (100 / 5) * nivel;
      const { text, color } = this.nivelConocimiento.find(
        item => item.nivel === nivel,
      );
      return { valor, text, color };
    },
    sortByProperty(item, property) {
      item.sort((a, b) => {
        let comparacion = 0;
        if (a[property] > b[property]) comparacion = -1;
        if (a[property] < b[property]) comparacion = 1;
        return comparacion;
      });
      return item;
    },
  },
};
</script>
<style>
.box {
